.future {
  padding: 100px 0;

  @include small-tablet {
    padding: 30px 0;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    gap: 25px;
    flex-wrap: wrap;
  }

  &__col-left {
    max-width: 436px;

    @include tablet {
      max-width: none;
    }
  }

  &__title {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value('font-size', 34, 20, 0);
    line-height: 45px;
    background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -text-fill-color: transparent;
    margin-bottom: 34px;
  }

  &__request {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--light-orange);
  }

  &__col-right {
    display: flex;
    flex-direction: column;
    gap: 30px;
    max-width: 590px;

    @include small-tablet {
      gap: 50px;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    gap: 25px;

    @include small-tablet {
      flex-direction: column;
      gap: 15px;
    }
  }

  &__info {
    flex: 0 1 182px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: var(--light);
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: -4px;
      left: 0;
      height: 3px;
      width: 38px;
      background: -o-linear-gradient(346.78deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
      background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    }

    @include small-tablet {
      flex: 0 0 auto;
    }
  }

  &__text {
    flex: 0 0 334px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--acva);

    @include small-tablet {
      flex: 0 0 auto;
    }
  }
}
