.request {

  &__container {
    display: flex;
    align-items: flex-end;
    gap: 20px;
    flex-wrap: wrap;

    @include tablet {
      justify-content: center;
    }
  }

  &__content {
    max-width: 480px;
    padding: 0 0 20px 0;

    @include tablet {
      max-width: none;
    }
  }

  &__head {
    display: inline-block;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--acva);
    margin-bottom: 10px;
  }

  &__title {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value("font-size", 32, 21, 0);
    line-height: 45px;
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%), #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -text-fill-color: transparent;
    margin-bottom: 23px;
  }

  &__text {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--acva);
    margin-bottom: 31px;
  }

  &__footer {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--light-orange);
  }
}
