.about {

  &__container {
    background-color: var(--light-blue);
    padding: 74px 55px 63px 57px;

    @include small-tablet {
      padding: 45px 25px;
    }
  }

  &__us {
    display: flex;
    justify-content: space-between;
    gap: 25px;
    margin-bottom: 91px;
    flex-wrap: wrap;
  }

  &__us-title {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 75px;
    letter-spacing: -0.04em;
    color: #FFFFFF;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 3px;
      width: 38px;
      background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    }
  }

  &__us-text {
    margin: 0;
    max-width: 731px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--acva);
  }

  &__possibilities {
    display: flex;
    justify-content: space-between;
    gap: 25px;
    margin-bottom: 97px;
    flex-wrap: wrap;
  }

  &__possibilities-title {
    margin: 0;
    max-width: 472px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -text-fill-color: transparent;
  }

  &__possibilities-text {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--light-orange);
  }

  &__work {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 35px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__work-head {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 75px;
    color: var(--light);
    margin-bottom: 26px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 3px;
      width: 38px;
      background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%), #FFFFFF;
    }
  }

  &__work-text {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--acva);
  }
}
