.nav {


  &__list {
    display: flex;
    gap: 34px;

    @include tablet {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }

  &__link {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 15, 0);
    line-height: 25px;
    color: var(--light);
    transition: 0.5s;

    &:hover {
      color: var(--orange);
    }
  }

  @include tablet {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 50;
    background-color: var(--light-blue);
    transform: translate(-100%, 0);
    transition: 0.5s;
  }
}

.burger {
  position: relative;
  z-index: 50;
  display: none;
  @include burger;

  @include tablet {
    display: block;
  }
}

.menu--active {
  transform: translate(0);
}
