.partners {
  padding: 110px 0;

  @include small-tablet {
    padding: 50px 0;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(5, 100px);
    justify-content: center;
    align-items: center;
    gap: 46px;

    @include small-tablet {
      grid-template-columns: repeat(3, 100px);
    }

    @include mobile {
      grid-template-columns: repeat(2, 100px);
    }
  }

  &__logo {
    img {
      width: 100%;
    }
  }
}
