.blog {
  padding: 20px 0 100px 0;


  &__title {
    max-width: 701px;
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value("font-size", 62, 25, 0);
    line-height: 75px;
    letter-spacing: -0.04em;
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -text-fill-color: transparent;
    margin-bottom: 115px;

    @include mobile {
      margin-bottom: 50px;
      line-height: 50px;
    }
  }

  &__content {
    display: flex;
    gap: 50px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    flex: 0 0 417px;
  }

  &__second {
    display: flex;
    justify-content: space-between;
    gap: 48px;
    flex: 0 1 700px;
    flex-wrap: wrap;
  }

  &__card-main {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  &__card-second {
    flex: 1 0 45%;
  }

  &__img {
    img {
      width: 100%;
      vertical-align: bottom;
      border-radius: 40px 2px 2px 2px;
    }
  }

  &__main-info {
    display: flex;
    flex-direction: column;
    padding: 32px 26px;
    background: #042C54;
    height: 100%;
  }

  &__second-info {
    padding: 7px 17px 13px 25px;
    background: #042C54;
  }


  &__data {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
    color: var(--light);
  }

  &__main-descr {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value("font-size", 25, 19, 0);
    line-height: 30px;
    color: var(--light);
  }

  &__second-descr {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-size: 19px;
    line-height: 30px;
    color: var(--light);
  }

  &__link {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 11.649px;
    line-height: 35px;
    color: var(--light);
    margin-top: auto;
  }
}
