.banner {
  padding: 150px 0;

  @include tablet {
    padding: 50px 0;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    border: 1px solid #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 46px 37px 29px 46px;

    @include tablet {
      padding: 25px;
      flex-direction: column;
      text-align: center;
    }
  }

  &__text {
    max-width: 800px;
  }

  &__head {
    display: inline-block;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: var(--black);

  }

  &__title {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value("font-size", 24, 15, 0);
    line-height: 45px;
    color: var(--black);
  }

  &__btn {
    align-self: start;
    background-color: var(--main-bg);
    border-radius: 40px;
    padding: 12px 45px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    @include adaptiv-value("font-size", 18, 14, 0);
    line-height: 30px;
    color: var(--light);

    @include tablet {
      align-self: center;
    }
  }
}
