.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 71px 0 15px 0;
  z-index: 9999;

  &__container {
    display: flex;
    align-items: center;

    @include tablet {
      justify-content: center;
    }
  }

  &__logo {
    position: relative;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    font-style: normal;
    text-transform: uppercase;
    color: var(--light);
    @include adaptiv-value("font-size", 21, 19, 0);
    max-width: 65px;
    width: 100%;
    transition: 0.5s;
    z-index: 999;

    &:hover {
      color: var(--orange);
    }
  }

  &__row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 65px;

    @include tablet {
      margin-left: 0;
      justify-content: center;
    }
  }

  &__btn {
    position: relative;
    display: flex;
    gap: 26px;
    z-index: 50;
  }

  &__sign-in {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 14, 0);
    line-height: 25px;
    color: var(--light);
    background-color: transparent;
    border-radius: 5px;
    padding: 16px 43px;
    transition: 0.5s;

    &:hover {
      background-color: var(--orange);
    }

    @include mobile {
      padding: 6px 8px;
      line-height: 16px;
    }
  }

  &__sign-up {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 14, 0);
    line-height: 25px;
    color: var(--light);
    background-color: var(--orange);
    border-radius: 5px;
    padding: 16px 43px;
    transition: 0.5s;

    @include mobile {
      padding: 6px 8px;
      line-height: 16px;
    }
  }
}
