.hero {
  padding: 220px 0 100px 0;
  z-index: 5;
  position: relative;
  overflow: hidden;

  @include small-tablet {
    padding: 200px 0 50px 0;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 30%;
    height: 70%;
    border-radius: 50%;
    background-color: var(--light-blue);
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(282px);
    z-index: 1;
  }

  &__container {
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    height: 100%;

    @include tablet {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 615px;

    @include tablet {
      max-width: none;
    }
  }

  &__title {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 800;
    @include adaptiv-value("font-size", 62, 42, 0);
    line-height: 75px;
    letter-spacing: -0.04em;
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -text-fill-color: transparent;
    margin-bottom: 33px;
  }

  &__text {
    margin: 0;
    max-width: 554px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    @include adaptiv-value("font-size", 20, 15, 0);
    line-height: 27px;
    color: var(--acva);
    margin-bottom: 33px;
  }

  &__form {
    height: 73px;
    display: flex;
    align-items: center;
    margin-bottom: 37px;

    @include mobile {
      flex-direction: column;
      height: auto;
      width: 100%;
    }
  }

  &__label {
    height: 100%;
    flex: 1 1 auto;

    @include mobile {
      width: 100%;
    }
  }

  &__input {
    display: inline-block;
    width: 100%;
    height: 100%;
    padding: 24px 15px 22px 32px;
    background-color: var(--blue);
    border-radius: 5px 0px 0px 5px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--light);
    outline: none;

    &::placeholder {
      color: #3d6184;
    }
  }

  &__btn {
    height: 100%;
    padding: 20px 26px 26px 34px;
    background-color: var(--orange);
    color: var(--light);
    border-radius: 0px 5px 5px 0px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;

    @include mobile {
      width: 100%;
    }
  }


  &__requests {
    display: flex;
    align-items: center;
    gap: 13px;
    flex-wrap: wrap;
  }

  &__requests-img {
    width: 181px;
  }

  &__requests-info {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: var(--light);
  }
}
