.footer {
  padding: 100px 0 27px 0;
  background: #031B34;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 25px;

    @include small-tablet {
      gap: 70px;
    }
  }

  &__left {
    max-width: 178px;
  }

  &__logo {
    display: inline-block;
    font-weight: 800;
    font-style: normal;
    text-transform: uppercase;
    color: var(--light);
    @include adaptiv-value("font-size", 35, 19, 0);
    transition: 0.5s;
    margin-bottom: 25px;

    &:hover {
      color: var(--orange);
    }
  }

  &__info {
    font-family: 'Manrope';
    font-style: normal;
    font-size: 12px;
    line-height: 14px;
    color: var(--light)
  }

  &__right {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include adaptiv-value("gap", 162, 50, 0);

    @include small-tablet {
      width: 100%;
    }
  }


  &__menu {
    max-width: 116px;
    display: flex;
    flex-direction: column;
    gap: 26px;
  }

  &__descr {
    margin: 0;
    font-family: 'Manrope';
    font-style: normal;
    font-size: 14px;
    line-height: 16px;
    color: var(--light);
    margin-bottom: 36px;
  }

  &__link {
    font-family: 'Manrope';
    font-style: normal;
    font-size: 12px;
    line-height: 16px;
    color: var(--light);
    transition: 0.5s;

    &:hover {
      color: var(--orange);
    }
  }

  &__text {
    margin-top: 83px;
    text-align: center;
    font-family: 'Manrope';
    font-style: normal;
    font-size: 12px;
    line-height: 14px;
    font-feature-settings: 'liga' off;
    color: var(--light);
  }
}
